          }">
          <TreeView
            v-if="url != ''"
            :url="url"
            @clicked="onRenderCode"/>
        </a-layout-sider>
        <a-layout-content 
          v-if="code.content != ''"
          :style="{ 
            padding: '0 24px', 
            minHeight: '280px',
            zIndex: '100',
          }">
          <template v-if="code.type == 'image'">
            <img :src="code.url"/>
          </template>
          <template v-else>
            <a-button type="primary" icon="copy" @click="onCopyCode">
              Copy
            </a-button>
