<script>
import TreeView from './TreeView'
import { component as VueCodeHighlight } from 'vue-code-highlight';
import Prism from 'vue-prism-component'
import 'prismjs/components/prism-python'
import 'prismjs/components/prism-docker'
import 'prismjs/components/prism-json'
import 'prismjs/components/prism-yaml'
import 'prismjs/components/prism-javascript'
import 'prismjs/components/prism-go'
import 'prismjs/components/prism-markdown'
import 'prismjs/components/prism-java'
import 'prismjs/components/prism-xml-doc'
import 'prismjs/components/prism-bash'
import 'prismjs/components/prism-makefile'
import copy from 'copy-to-clipboard';

//import loadLanguages from 'prismjs/components/index'

//loadLanguages(['python', 'php', 'javascript'])

export default {
  name: 'Home',
  components: {
    TreeView,
    VueCodeHighlight,
    Prism,
  },
  data(){
    return {
      code: {
        language: 'python',
        content: '',
        url: '', // when type == image
        type: 'code', // image, code
      },
      url: "http://automation/data.json",
      dataurl: 'http://quarkus-lab:8000/projects/agoncal-rest-book/',
      url: "",
      dataurl: '',
    }
  },
  mounted(){
    //console.log(this.$route)
    this.$set(this, 'url', this.$route.query.metajson)
    this.$set(this, 'dataurl', this.$route.query.dataurl)
    console.log(this.url)
    console.log(this.dataurl)
  },
  methods: {
    onCopyCode(){
      copy(this.code.content)
    },
    onRenderCode(path){
      const images = ['png', 'jpeg', 'jpg', 'gif', 'svg']
      const extmime = {
        sh: 'bash',
        bash: 'bash',
        markdown: 'markdown',
        py: 'python',
        go: 'go',
        makefile: 'makefile',
        java: 'java',
        javascript: 'javascript',
        xml: 'xml',
        docker: 'docker',
        dockerfile: 'docker',
        json: 'json',
        yaml: 'yaml',
        html: 'html',
      }
      const ext = path.split('.').pop().toLowerCase()
      let prismext = extmime[ext]
      if (!prismext && (ext == 'Dockerfile' || path.toLowerCase().includes('dockerfile'))){
        prismext = 'docker'
      }
      if (images.includes(ext)){
        this.code.url = this.dataurl+path
        this.code.type = 'image'
      }else{
        this.code.type = 'code'
        this.code.language = prismext
        this.axios.get(this.dataurl+path).then(rs => {
          this.code.content2 = rs.data
          this.code.content = rs.data
        })
      }
    }
  }
}
</script>

